  padding: 15px 0px;
  border-bottom: 1px solid;
  z-index: 1;
}

.main-header-one__top-pattern {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: -999999px;
  background-attachment: scroll;
  background-repeat: repeat-x;
  background-position: center center;
  z-index: -1;
}

.main-header-one__top-pattern::before {
