}

.main-header-two__top-right .inner .btn-box a {
  position: relative;
  display: inline-block;
  color: var(--thm-white);
  font-size: 16px;
  line-height: 50px;
  font-weight: 700;
  padding: 0px 40px 0px;
  transition: all 200ms linear;
  transition-delay: 0.1s;
  z-index: 1;
}

.main-header-two__top-right .inner .btn-box a:hover {
